@font-face {
  font-family: "entypo";
  src: url('../fonts/entypo.eot');
  src: url('../fonts/entypo.eot?#iefix') format('eot'),
       url('../fonts/entypo.woff2') format('woff2'),
       url('../fonts/entypo.woff') format('woff'),
       url('../fonts/entypo.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.icon:before {
  position: relative;
  top: 2px;
  display: inline-block;
  font-family: "entypo";
  speak: none;
  font-size: 100%;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// carousel overrides
.carousel {
  .icon-chevron-thin-left,
  .icon-chevron-thin-right {
    position: absolute;
    top: 50%;
    margin-top: -10px;
    z-index: 5;
    display: inline-block;
  }
  .icon-chevron-thin-left {
    left: 50%;
    margin-left: -10px;
  }
  .icon-chevron-thin-right {
    right: 50%;
    margin-right: -10px;
  }
}

